@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css?family=Open+Sans");
@import url("https://fonts.googleapis.com/css2?family=Kanit:wght@200&display=swap");
@import url("https://fonts.googleapis.com/css?family=Sarabun");

html,
body,
a,
label,
h1,
h2,
h3,
h4,
h5,
p,
span {
  font-family: "Sarabun", sans-serif !important;
  /* font-weight: 900;*/
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.alert_table {
  font-family: "sarabun";
  border-collapse: collapse;
  width: 100%;
  color: #fff;
}
.ant-table-filter-dropdown-btns button:nth-child(2){
  background-color: #137b0c  !important;
}

.alert_table td,
.alert_table th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
  font-weight: 400;
}

.alert_table tr:nth-child(even) {
  background-color: #162c3f;
}
div:where(.swal2-container) .swal2-html-container {
  margin: 0px !important;
}
.deposit_page .ant-segmented .ant-segmented-item-selected {
  background-color: #193359;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.03),
    0 1px 6px -1px rgba(0, 0, 0, 0.02), 0 2px 4px 0 rgba(0, 0, 0, 0.02);
  color: rgba(255, 255, 255, 0.85);
}
.btn-home {
  background-image: linear-gradient(144deg, #3e4775, #414141 50%, #222933);

  box-shadow: rgba(43, 39, 70, 0.2) 0 15px 30px -5px;
  box-sizing: border-box;
  transition: 5ms;
}

.btn-home:active,
.btn-home:hover {
  filter: saturate(2);
  border: 1px solid #dddddd;
}
.btn-home2 {
  background-image: linear-gradient(343deg, #fbac30, #5d46dd 50%, #5d8edc);
  box-shadow: rgba(43, 39, 70, 0.2) 0 15px 30px -5px;
  box-sizing: border-box;
  transition: 5ms;
}

.btn-home2:active,
.btn-home2:hover {
  filter: saturate(1.2);
  border: 1px solid #a8a8a8;
}
.white_text .MuiPaginationItem-page,
.white_text .MuiPaginationItem-icon {
  color: #fff !important;
}

.text-hover-show {
  width: 550px;

  text-overflow: ellipsis;
  cursor: pointer;
  word-break: break-all;
  overflow: hidden;
  white-space: nowrap;
}
@media only screen and (max-width: 800px) {
  .text-hover-show {
    width: 200px;
  }
}
.text-hover-show-admin {
  width: 100%;

  text-overflow: ellipsis;
  cursor: pointer;
  word-break: break-all;
  overflow: hidden;
  white-space: nowrap;
}

.text-hover-show:hover,
.text-hover-show-admin:hover {
  overflow: visible;
  white-space: normal;
  height: auto; /* just added this line */
  text-wrap: wrap;
}
.text-shadow-1 {
  text-shadow: 0px 1px 2px #000;
}
.text-shadow-2 {
  text-shadow: 0px 1px 1px #000;
}

::-webkit-scrollbar {
  height: 4px;
  width: 4px;
  background: gray;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

::-webkit-scrollbar-thumb:horizontal {
  background: #000;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:vertical {
  background: #000;
  border-radius: 10px;
}

.gecko-static-group {
  display: flex;
  justify-content: center !important;
}
