.progress-container1 {
  position: relative;
}

.custom-label {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}

.progress-bar {
  width: 100%;
  height: 15px;
  background-color: lightgrey;
  border-radius: 15px;
  position: relative;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.4) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.4) 50%,
    rgba(255, 255, 255, 0.4) 75%,
    transparent 75%,
    transparent
  );
  background-size: 40px 40px;
  position: relative;
  animation: move 2s linear infinite;
  transition: width 1s ease-in-out;
  border-radius: 50px;
}

@keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 40px 0;
  }
}

.progress-label {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 46%;
  transform: translateY(-50%);

  font-weight: bold;
  z-index: 1; /* ทำให้ตัวหนังสืออยู่บนสุด */
}

.slick-slider {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
}
.slick-slide {
  padding: 0 10px; /* ปรับระยะห่างระหว่างสไลด์ตามต้องการ */
}

.slick-slide img {
  width: 100%;
  height: auto;
  object-fit: cover;
}
.full-width-slider {
  width: 100%;
  overflow: hidden;
}
@media only screen and (max-width: 992px) {
  .progress-bar {
    height: 13px;
  }
