.custom-input {
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-color: transparent;
}

.custom-input:focus {
  border-color: #8b5cf6;
}

.custom-input:-webkit-autofill,
.custom-input:-webkit-autofill:hover,
.custom-input:-webkit-autofill:focus,
.custom-input:-webkit-autofill:active {
  -webkit-background-clip: text;
  -webkit-text-fill-color: white !important;
  transition: background-color 5000s ease-in-out 0s;
  box-shadow: inset 0 0 20px 20px rgba(255, 255, 255, 0.1);
}

.updown1 {
  animation: x-updown1 2s ease-in-out infinite;
}
@keyframes x-updown1 {
  0% {
    transform: translateY(0) rotate(0);
  }
  50% {
    transform: translateY(-6px) rotate(-1deg);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}
.updown2 {
  animation: x-updown2 1.5s ease-in-out infinite;

}
@keyframes x-updown2 {
  0% {
    transform: translateY(0) rotate(0);
    -webkit-filter: drop-shadow(0px 0px -1px #fbff06);
    filter: drop-shadow(0px 0px -1px #fbff06);
  }
  50% {
    transform: translateY(-6px) rotate(-1deg);
    -webkit-filter: drop-shadow(0px 0px 5px #fbff06);
    filter: drop-shadow(0px 0px 5px #fbff06);
  }
  100% {
    transform: translateY(0) rotate(0);
    -webkit-filter: drop-shadow(0px 0px 1px #fbff06);
    filter: drop-shadow(0px 0px 1px #fbff06);
  }
}

.shake_icon {
  animation: shakeAndScale 1.5s ease-in-out infinite both;
}

@keyframes shakeAndScale {
  0% {
    transform: scale(1, 1);
  }

  50% {
    transform: scale(1.2, 1.2);
  }

  100% {
    transform: scale(1, 1);
  }
}



/* Fix สำหรับ iOS Safari */
@supports (-webkit-touch-callout: none) {
  .icon-position {
    transform: translateX(-40%);
    left: -4px !important;
  }
  

}

